import axios from "axios";
import { BACKEND_URL } from "../constants";

export function sendDatatoServer(data) {
  const { file, link } = data;
  let body = { ...data, input: link };
  if (file) {
    body = new FormData();
    body.append("myFile", file, file.name);
    body.append("name", data.name);
    body.append("type", data.type);
  }
  return axios.post(BACKEND_URL + "/submit", body);
}

export function getItems(callback) {
  axios
    .get(BACKEND_URL + "/all")
    .then((res) => {
      callback(res.data);
    })
    .catch((err) => console.error(err));
}

export async function updateStatus(jobs) {
  const results = [];
  for (let job in jobs) {
    const res = await (
      await axios.post(BACKEND_URL + "/status", { itemId: jobs[job].itemId })
    ).data;
    results.push({ ...jobs[job], status: res });
  }
  return results;
}

export function getAllTopics() {
  return [];
}

export function getJobDetails(job) {
  return axios.post(BACKEND_URL + "/result", { itemId: job.itemId });
}

export function getDashboardData() {
  return axios.get(BACKEND_URL + "/dashboard");
}
