import axios from "axios";
import { BACKEND_URL } from "../../constants";

export default function putToDashboardDB(conversation, callback) {
  const req = {
    conversationId: conversation.conversationId,
    jsonData: JSON.stringify(conversation, 2),
  };
  axios
    .post(BACKEND_URL + "/dashboard", req)
    .then((data) => {
      callback(data);
    })
    .catch((err) => console.error(err));
}
