import getForDashboard from "../../utils/db/getForDashboard";
import putToDashboardDB from "../../utils/db/putToDashboardDB";
import getConversation from "./getConversation";
import getConversationDetails from "./getConversationDetails";
import getSummary from "./getSummary";

export const params = {
  topics: true,
  followUps: true,
  summary: true,
  members: true,
  questions: true,
  actionItems: true,
  entities: true,
  metrics: true,
  analytics: true,
};

async function fetchConversationItem(req) {
  if (req.params) {
  }

  const res = await getConversation(req);
  let conversation = { ...res.data };
  conversation["conversationId"] = conversation.id;

  if (params.topics) {
    const res = await getConversationDetails(req, "topics");
    conversation = { ...conversation, ...res.data };
  }
  if (params.followUps) {
    const res = await getConversationDetails(req, "follow-ups");
    conversation = { ...conversation, ...res.data };
  }
  if (params.summary) {
    const res = await getSummary(req);
    conversation = { ...conversation, ...res.data };
  }
  if (params.questions) {
    const res = await getConversationDetails(req, "questions");
    conversation = { ...conversation, ...res.data };
  }
  if (params.actionItems) {
    const res = await getConversationDetails(req, "action-items");
    conversation = { ...conversation, ...res.data };
  }
  if (params.entities) {
    const res = await getConversationDetails(req, "entities");
    conversation = { ...conversation, ...res.data };
  }
  if (params.analytics) {
    const res = await getConversationDetails(req, "analytics");
    conversation = { ...conversation, metrics: res.data.metrics, analytics: res.data.members };
  }
  putToDashboardDB(conversation, () => {});
  return conversation;
}

export default async function getConversationItem(req) {
  const data = await getForDashboard(req.conversationId);
  if (data.status === 200 && data.data) return JSON.parse(data.data.jsonData);
  return await fetchConversationItem(req);
}
