export default function getPieChartOptions({ series, labels }, showLabel = false) {
  return {
    series: series,
    options: {
      chart: {
        width: 80,
        type: "pie",
      },
      legend: {
        show: showLabel,
      },
      labels: labels,
      theme: {
        palette: "palette10",
      },
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 80,
            },
            legend: {
              position: "bottom",
            },
          },
        },
      ],
    },
  };
}
