import React, { useEffect, useState } from "react";

// material-ui
import { Grid } from "@material-ui/core";

// project imports
import { gridSpacing } from "../../store/constant";
import getAllDetails from "../../utils/db/details/getAllDetails";
import TopicsCard from "./TopicsCard";
import AnalyticsChart from "./AnalyticsChart";
import SummaryCard from "./SummaryCard";
import EntitiesCard from "./EntitiesCard";
import { getDashboardData } from "../../utils";

//-----------------------|| DEFAULT DASHBOARD ||-----------------------//

const getEntities = (data) => {
  const entities = [];
  for (let i = 0; i < data.length; i++) {
    if (data[i].result.nameEntityRecognition && data[i].result.nameEntityRecognition.results) {
      entities.push(data[i].result.nameEntityRecognition.results);
    }
  }
  return entities.flat();
};

const Dashboard = () => {
  const [isLoading, setLoading] = useState(true);
  const [conversations, setConversations] = useState([]);
  const [data, setData] = useState({});
  useEffect(() => {
    setLoading(false);
    getDashboardData().then((data) => {
      setData(data.data);
      setConversations(data.data);
      setLoading(false);
      console.log("dashboard", data.data);
    });
  }, []);

  useEffect(() => {
    getAllDetails(conversations).then((data) => setData(data));
  }, [conversations]);

  const entities = { nameEntityRecognition: { results: getEntities(data) } };

  return (
    <Grid container spacing={gridSpacing}>
      <Grid item xs={12}>
        <Grid container spacing={gridSpacing}>
          <Grid item lg={4} md={6} sm={6} xs={12}>
            <TopicsCard isLoading={isLoading} data={data} />
          </Grid>
          <Grid item lg={4} md={6} sm={6} xs={12}>
            <EntitiesCard isLoading={isLoading} data={entities} />
          </Grid>
          <Grid item lg={4} md={6} sm={6} xs={12}>
            <AnalyticsChart isLoading={isLoading} data={data} />
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <SummaryCard isLoading={isLoading} data={data} />

        <Grid container spacing={gridSpacing}></Grid>
      </Grid>
    </Grid>
  );
};

export default Dashboard;
