import axios from "axios";

var secret = JSON.stringify({
  type: "application",
  appId: "",
  appSecret:"",
});

export default function getToken() {
  var config = {
    method: "post",
    url: "https://api.symbl.ai/oauth2/token:generate",
    headers: {
      "Content-Type": "application/json",
    },
    data: secret,
  };
  return axios(config);
}
