import PropTypes from "prop-types";
import React from "react";

// material-ui
import { makeStyles } from "@material-ui/styles";
import {
  Button,
  CardActions,
  CardContent,
  Divider,
  Grid,
  Menu,
  MenuItem,
  Typography,
} from "@material-ui/core";

// project imports
import MainCard from "../../ui-component/cards/MainCard";
import SkeletonPopularCard from "../../ui-component/cards/Skeleton/PopularCard";
import { gridSpacing } from "../../store/constant";

// assets
import ChevronRightOutlinedIcon from "@material-ui/icons/ChevronRightOutlined";
import MoreHorizOutlinedIcon from "@material-ui/icons/MoreHorizOutlined";

// style constant
const useStyles = makeStyles((theme) => ({
  cardAction: {
    padding: "10px",
    paddingTop: 0,
    justifyContent: "center",
  },
  primaryLight: {
    color: theme.palette.primary[200],
    cursor: "pointer",
  },
  divider: {
    marginTop: "12px",
    marginBottom: "12px",
  },
  avatarSuccess: {
    width: "16px",
    height: "16px",
    borderRadius: "5px",
    backgroundColor: theme.palette.success.light,
    color: theme.palette.success.dark,
    marginLeft: "15px",
  },
  successDark: {
    color: theme.palette.success.dark,
  },
  avatarError: {
    width: "16px",
    height: "16px",
    borderRadius: "5px",
    backgroundColor: theme.palette.orange.light,
    color: theme.palette.orange.dark,
    marginLeft: "15px",
  },
  errorDark: {
    color: theme.palette.orange.dark,
  },
}));

//-----------------------|| DASHBOARD DEFAULT - POPULAR CARD ||-----------------------//

const getSummary = (data) => {
  const summaries = [];
  for (let i = 0; i < data.length; i++) {
    if (data[i].result.textSummary && data[i].result.textSummary.results) {
      summaries.push({
        name: data[i].name,
        type: data[i].type,
        timestamp: data[i].timestamp,
        summary: data[i].result.textSummary.results.summary,
      });
    }
  }
  return summaries;
};

const SummaryCard = ({ isLoading, data }) => {
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const summaries = getSummary(data);
  console.log(summaries);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <React.Fragment>
      {isLoading ? (
        <SkeletonPopularCard />
      ) : (
        <MainCard content={false}>
          <CardContent>
            <Grid container spacing={gridSpacing}>
              <Grid item xs={12}>
                <Grid container alignContent="center" justifyContent="space-between">
                  <Grid item>
                    <Typography variant="h4">Text Summary</Typography>
                  </Grid>
                  <Grid item>
                    <MoreHorizOutlinedIcon
                      fontSize="small"
                      className={classes.primaryLight}
                      aria-controls="menu-popular-card"
                      aria-haspopup="true"
                      onClick={handleClick}
                    />
                    <Menu
                      id="menu-popular-card"
                      anchorEl={anchorEl}
                      keepMounted
                      open={Boolean(anchorEl)}
                      onClose={handleClose}
                      variant="selectedMenu"
                      anchorOrigin={{
                        vertical: "bottom",
                        horizontal: "right",
                      }}
                      transformOrigin={{
                        vertical: "top",
                        horizontal: "right",
                      }}
                    >
                      <MenuItem onClick={handleClose}> Today</MenuItem>
                      <MenuItem onClick={handleClose}> This Month</MenuItem>
                      <MenuItem onClick={handleClose}> This Year </MenuItem>
                    </Menu>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12}>
                {summaries.map((e, i) => {
                  return (
                    <div key={i}>
                      <Grid container direction="column">
                        <Grid item>
                          <Grid container alignItems="center" justifyContent="space-between">
                            <Grid item>
                              <Typography variant="subtitle1" color="inherit">
                                {e.summary}
                              </Typography>
                              {e.name}
                            </Grid>
                          </Grid>
                        </Grid>
                        <Grid item style={{ display: "flex", flexDirection: "row" }}>
                          <Typography variant="subtitle2" className={classes.successDark}>
                            {e.timestamp}
                          </Typography>
                          <Typography variant="subtitle2">{e?.from?.userId}</Typography>
                        </Grid>
                      </Grid>
                      <Divider className={classes.divider} />
                    </div>
                  );
                })}
              </Grid>
            </Grid>
          </CardContent>
          <CardActions className={classes.cardAction}>
            <Button size="small" disableElevation>
              View All
              <ChevronRightOutlinedIcon />
            </Button>
          </CardActions>
        </MainCard>
      )}
    </React.Fragment>
  );
};

SummaryCard.propTypes = {
  isLoading: PropTypes.bool,
};

export default SummaryCard;
