import PropTypes from "prop-types";
import React from "react";

// material-ui
import { makeStyles } from "@material-ui/styles";
import { Card, CardContent, CardHeader, Chip, Divider, Grid, Typography } from "@material-ui/core";

// project imports
import MainCard from "./../../ui-component/cards/MainCard";
import SkeletonPopularCard from "./../../ui-component/cards/Skeleton/PopularCard";
import { gridSpacing } from "./../../store/constant";

// style constant
const useStyles = makeStyles((theme) => ({
  cardAction: {
    padding: "10px",
    paddingTop: 0,
    justifyContent: "center",
  },
  primaryLight: {
    color: theme.palette.primary[200],
    cursor: "pointer",
  },
  divider: {
    marginTop: "12px",
    marginBottom: "12px",
  },
  avatarSuccess: {
    width: "16px",
    height: "16px",
    borderRadius: "5px",
    backgroundColor: theme.palette.success.light,
    color: theme.palette.success.dark,
    marginLeft: "15px",
  },
  successDark: {
    color: theme.palette.success.dark,
  },
  avatarError: {
    width: "16px",
    height: "16px",
    borderRadius: "5px",
    backgroundColor: theme.palette.orange.light,
    color: theme.palette.orange.dark,
    marginLeft: "15px",
  },
  errorDark: {
    color: theme.palette.orange.dark,
  },
}));

//-----------------------|| DASHBOARD DEFAULT - POPULAR CARD ||-----------------------//

const getEntities = (data) => {
  const results = [];
  for (let i = 0; i < data.length; i++) {
    if (data[i][1].includes("PER")) results.push({ type: "Person", value: data[i][0] });
    if (data[i][1].includes("LOC")) results.push({ type: "Location", value: data[i][0] });
    if (data[i][1].includes("ORG")) results.push({ type: "Organisation", value: data[i][0] });
    if (data[i][1].includes("MISC")) results.push({ type: "Miscellaneous", value: data[i][0] });
  }
  return results;
};

const EntitiesCard = ({ isLoading, data }) => {
  const classes = useStyles();

  // const [anchorEl, setAnchorEl] = React.useState(null);

  const entities = getEntities(
    data.nameEntityRecognition ? data.nameEntityRecognition.results : [],
  );

  // const handleClick = (event) => {
  //   setAnchorEl(event.currentTarget);
  // };

  // const handleClose = () => {
  //   setAnchorEl(null);
  // };

  return (
    entities.length > 0 && (
      <React.Fragment>
        {isLoading ? (
          <SkeletonPopularCard />
        ) : (
          <MainCard content={false}>
            <CardContent>
              <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                  <Grid container direction="column">
                    <Grid item>
                      <Grid container alignItems="center" justifyContent="space-between">
                        <Grid item>
                          <Typography variant="h4" color="inherit">
                            Named Entities
                          </Typography>
                        </Grid>
                      </Grid>
                    </Grid>
                    <Grid item sx={{ display: "flex", flexWrap: "wrap" }}>
                      {entities?.map((e, i) => (
                        <Card key={i} style={{ margin: 2, textAlign: "center" }}>
                          <CardHeader
                            title={e?.name}
                            subheader={e?.type}
                            style={{ margin: 0, padding: 5 }}
                          />
                          <Chip label={e.value} />
                        </Card>
                      ))}
                      <br />
                    </Grid>
                  </Grid>
                  <Divider className={classes.divider} />
                </Grid>
              </Grid>
            </CardContent>
          </MainCard>
        )}
      </React.Fragment>
    )
  );
};

EntitiesCard.propTypes = {
  isLoading: PropTypes.bool,
};

export default EntitiesCard;
