import getConversationItem, { params } from "../../../symbl.ai/conversation";

function getItems(data, key, conversationId) {
  if (data.hasOwnProperty(key)) {
    return data[key].map((e, i) => {
      return { ...e, conversationId };
    });
  }
  return [];
}

async function getConversationDetails(conversationId, allData) {
  const data = await getConversationItem({
    conversationId: conversationId,
    params: {},
  });
  Object.keys(params).map((key) => {
    if (allData.hasOwnProperty(key)) {
      allData[key] = [...allData[key], ...getItems(data, key, conversationId)];
    } else {
      allData[key] = getItems(data, key, conversationId);
    }
    return "";
  });

  if (allData.hasOwnProperty("duration"))
    allData["duration"].push(0.001 * (new Date(data["endTime"]) - new Date(data["startTime"])));
  else allData["duration"] = [0.001 * (new Date(data["endTime"]) - new Date(data["startTime"]))];

  return allData;
}

export default async function getAllDetails(conversations) {
  let allData = {};
  for (let i = 0; i < conversations.length; i++)
    allData = await getConversationDetails(conversations[i].conversationId, allData);

  if (allData.hasOwnProperty("actionItems")) {
    for (let i = 0; i < allData["actionItems"].length; i++) {
      const e = allData["actionItems"][i];
      const x = e["entities"] ? e["entities"] : [];
      allData["entities"] = [...allData["entities"], ...x];
    }
  }
  return allData;
}
